import {useState} from 'react'
import { Link } from 'react-router-dom';
import { SearchIcon } from '../icons/Icon'
import eatzy from '../images/eatzy.png'
import './header.css'

const Header = () => {
    
    const [active, setActive] = useState('home');
    
    const options = [
        'Home',
        'List',
        'Detail'
    ]

    const slugify = Text => {
        return Text.toLowerCase()
            .replace(/ /g, '')
            .replace(/[^\w-]+/g, '');
    };

    const choice = options.map(option => slugify(option));
    console.log(choice)

    const activeClass = () => {
        if(choice === 'home') {
            setActive('home')
        } else if(choice === 'list') {
            setActive('list')
        } else if(choice === 'detail') {
            setActive('detail')
        }
    }

    return (
        <div className='header' >
            <div className='header-top row' >
                <img className='col-xs-4 col-md-4' src={eatzy} width='200px' height='80px' alt='eatzy logo' />
                <div className='options col-xs-3 col-md-6' >
                    <div className='option'>
                        {options && options.map((option, index) => (
                            <Link key={index} className='tag'
                                to={`/${slugify(option)}`} onClick={activeClass}>{option}
                                <div className={active === choice ? 'active-tag' : ''}    /></Link>
                                
                        ))}
                    </div>
                    <SearchIcon />
                </div>
            </div>
            <div className='header-border row' />
        </div>
    )
}

export default Header