import {Helmet} from 'react-helmet'
import { lists } from '../lists'
import './list.css'

const List = () => {
  console.log(lists);
  return (
    <div className="list">
       <Helmet>
        <title>List</title>
      </Helmet>
      <ul className='list-group'>
        {
          lists.map(list => {
            return (
              <li key={list.id} className='list-items'>
                <img src={list.image} width='380px' height='280px' alt={list.description} />
                <div className='list-info'>
                  <h5 className='list-type'>{list.type}</h5>
                  <h5 className='list-description'>{list.description}</h5>
                </div>
              </li>
            )
          })
        }
      </ul>


    </div>
  )
}

export default List